.blogHeader__image {
  position: relative;
  z-index: 1;
  margin-top: -93px;
  border: 10px solid #006dfe;
  height: 428px;
  background-size: cover;
  background-position: 50% 50%;
}
@media (max-width: 1199px) {
  .blogHeader__image {
    margin-top: 0;
  }
}
.blogHeader__image:after {
  content: '';
  position: absolute;
  width: 84px;
  height: 5px;
  background: #1d1d1d;
  top: 100%;
  left: 0;
  margin-top: 10px;
  margin-left: -10px;
}
.blogHeader__sup {
  display: flex;
