    display: block;
    margin-right: auto;
  }
}
.blogHeader__time {
  margin-right: auto;
}
@media (max-width: 767px) {
  .blogHeader__time {
    margin-bottom: 8px;
  }
}
.blogHeader__time:before,
.blogHeader__date:before {
  content: '';
  width: 6px;
  height: 6px;
  display: inline-block;
  vertical-align: middle;
  margin-right: 8px;
  background: #006dfe;
}
.blogHeader__share {
  margin-left: 48px;
}
@media (max-width: 1199px) {
