.blogAbout__metrik {
  position: relative;
  margin-right: 25px;
  padding-left: 31px;
}
.blogAbout__metrik:last-child {
  margin-right: 0;
}
.blogAbout__metrik svg {
  position: absolute;
  fill: #006dfe;
  stroke: #006dfe;
  width: 20px;
  height: 20px;
  left: 14px;
  top: 50%;
  transform: translate(-50%, -50%);
}
.blogAbout__share {
  margin-top: 36px;
}
.blogAbout__link {
  position: relative;
  border: 15px solid #006dfe;
  font-size: 48px;
  line-height: 1;
  display: flex;
  align-items: center;
  justify-content: center;
  padding: 0 0 0 74px;
  transition: all 0.1s ease-in;
}
@media (max-width: 1199px) {
  .blogAbout__link {
    width: 100%;
    margin-top: 40px;
    padding: 51px 0 51px 41px;
  }
}
@media (max-width: 767px) {
