    margin-top: 34px;
    padding: 38px 0 32px 36px;
  }
}
.blogAbout__link:hover {
  background: #006dfe;
  color: #fff;
}
.blogAbout__link:after {
  content: '';
  position: absolute;
  width: 72px;
  height: 7px;
  background: #000;
  top: 100%;
  right: 0;
  margin-top: 15px;
  margin-right: -15px;
}
